 * @param  {[type]} $support-for-ie:true [是否支持ie  默认支持]
 */
/**
 * 绝对定位版 绝对居中
 */
/**
 * 全屏遮罩
 */
/**
 * 使用原生滚动 & 隐藏滚动条
 */
/**!
 * from reset.scss
 */
/**!
 * from rem.scss
 */
/**!
 * from media.scss
 */
/**
 * 213px显示屏样式 LG Optimus One
 * 320px显示屏样式 苹果4/4S/5C/5S黑莓Z30
 * 360px显示屏样式 索尼Z1
 * 384px显示屏样式 黑莓Z10 谷歌 Nexus 6 LG Optimus G
 * 399px显示屏样式 三星galaxyNote
 * 414px显示屏样式 苹果6plus
 * 424px显示屏样式 LG 4X
 * 480px显示屏样式 索尼MT27i Xperia sola
 * 640px显示屏样式 摩托罗拉Droid3/4/Razr Atrix 4g
 * 640px显示屏样式
 * 640px以上显示屏样式
 */
@media screen and (min-width: 212px) {
  html {
    font-size: 13.3125px; } }

@media screen and (min-width: 319px) {
  html {
    font-size: 20px; } }

@media screen and (min-width: 359px) {
  html {
    font-size: 22.5px; } }

@media screen and (min-width: 383px) {
  html {
    font-size: 24px; } }

@media screen and (min-width: 399px) {
  html {
    font-size: 25px; } }

@media screen and (min-width: 414px) {
  html {
    font-size: 25.9375px; } }

@media screen and (min-width: 423px) {
  html {
    font-size: 26.5px; } }

@media screen and (min-width: 479px) {
  html {
    font-size: 30px; } }

@media screen and (min-width: 539px) {
  html {
    font-size: 33.75px; } }

@media screen and (min-width: 639px) {
  html {
    font-size: 40px; } }

@media screen and (min-width: 640px) {
  html {
    font-size: 40.0625px; } }

@media only screen and (min-device-width: 320px) and (max-device-width: 568px) {
  html {
    font-size: 20px; } }

@media (device-width: 375px) and (-webkit-min-device-pixel-ratio: 2) {
  html {
    font-size: 23.4375px; } }

@media (device-width: 414px) and (-webkit-min-device-pixel-ratio: 3) {
  html {
    font-size: 25.875px; } }

