/**
 * 根据当前设计稿进行单位转换
 * $isPX   {[boolean]}    	[是否使用px单位 	true => 始终返回640比例的尺寸   false => 返回rem单位]
 * $design {[number]} 	  	[当前设计稿尺寸	640/750]
 * $output {[number]} 	  	[需要输出的数值类型  640/750 	修改数值需修改对应的js   640=>rem.js   750=>flexible.js]
 * @param  {[number]} $px 	[不带单位的数值]
 * 
 * @return {[string]}     	[返回rem或px 取决于$isPX]
 */
/**!
 * from reset.scss
 */
/**!
 * from rem.scss
 */
/**!
 * from media.scss
 */
.fl {
  float: left; }

.fr {
  float: right; }

.clearfix {
  *zoom: 1; }
  .clearfix:before, .clearfix:after {
    content: "";
    display: table; }
  .clearfix:after {
    clear: both;
    overflow: hidden; }

.f12 {
  font-size: 12px; }

.f14 {
  font-size: 14px; }

.f16 {
  font-size: 16px; }

.f18 {
  font-size: 18px; }

.f20 {
  font-size: 20px; }

.f22 {
  font-size: 22px; }

.f24 {
  font-size: 24px; }

body {
  position: relative;
  z-index: 101;
  background: #fff; }

pre {
  margin: 0; }

.half640 {
  width: 6.82667rem;
  height: 6.82667rem;
  border: 1px solid #f00;
  box-sizing: border-box; }

.half750 {
  width: 8rem;
  height: 8rem;
  border: 1px solid #f00;
  box-sizing: border-box; }
